.imageslider-caption {
	padding-top: 1rem;
	padding-bottom: 1rem;
	color: #fff;
	background-color: #000;
	text-align: center;
	h5 {
		font-size: 16px;
		font-weight: bold;
	}
	p {
		font-size: 12px;
		margin-bottom: 0;
	}
}

@include media-breakpoint-up(md) {
	.imageslider-caption {
		position: absolute;
		right: 15%;
		bottom: 1.25rem;
		left: 15%;
		padding-top: 1.25rem;
		padding-bottom: 1.25rem;
		color: #fff;
		text-align: center;
	}
}
